.header {
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__image {
  height: 40px;
}

.header a > img {
  justify-content: center;
  align-items: center;
  display: flex;
}

.header__navigation {
  align-items: center;
  height: 60px;
  display: flex;
}

.header__navigation-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.header__navigation-item:not(:first-child) {
  margin-left: 20px;
}

.header__navigation-link {
  text-transform: uppercase;
  text-align: center;
  color: #060b35;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  text-decoration: none;
  transition-duration: .3s;
  transition-timing-function: linear;
  display: flex;
}

.header__navigation-link:hover {
  color: #00acdc;
}

.header .is-active {
  color: #00acdc;
  position: relative;
}

.header .is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.cards-display {
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width <= 487px) {
  .cards-display {
    grid-template: auto / 200px;
  }
}

@media (width >= 488px) {
  .cards-display {
    grid-template: auto / repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .cards-display {
    grid-template: auto / repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .cards-display {
    grid-template: auto / repeat(4, 200px);
  }
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  width: 200px;
  padding: 16px;
  font-family: Roboto, sans-serif;
  transition-duration: .3s;
  transition-timing-function: linear;
  display: flex;
}

.card__picture {
  width: 160px;
  height: 134px;
  margin-top: 16px;
}

.card__item-model {
  color: #060b35;
  margin-top: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__item-code {
  color: #616070;
  width: 100%;
  margin-top: 4px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__item-review {
  box-sizing: border-box;
  justify-content: space-between;
  align-items: flex-end;
  width: 100%;
  margin-top: 16px;
  display: flex;
}

.card__review {
  color: #060b35;
  white-space: nowrap;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__item-price {
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-top: 24px;
  display: flex;
}

.card__item-price-word {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__item-price-amount {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 16px;
}

.card__button {
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  margin-top: 16px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  transition-duration: .3s;
  transition-timing-function: linear;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__item-model {
  color: #34568b;
}

.stars {
  justify-content: flex-start;
  display: flex;
}

.stars__star {
  background-image: url("star.0aa15587.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--1 .stars__star:first-child, .stars--2 .stars__star:nth-last-child(n+4), .stars--3 .stars__star:nth-last-child(n+3), .stars--4 .stars__star:nth-last-child(n+2), .stars--5 .stars__star:nth-last-child(n+1) {
  background-image: url("star-active.1a24ce14.svg");
}

* {
  margin: 0;
  padding: 0;
}

body {
  font-family: Roboto, sans-serif;
}
